/******************** Start Navigator Section */
// define null item
let lastHoverElem = null;
window.openSubMenu = function (id_) {
  // select element by id
  const nav_link = document.querySelector(id_);
  // if there is an item has assign before
  // hide that item
  if (lastHoverElem != null) {
    lastHoverElem.classList.remove("displayed");
  }
  // show the selected item
  nav_link.classList.add("displayed");
  // assign selected item to a variable
  lastHoverElem = nav_link;
};

window.closeSubMenu = function (id_) {
  // select an element by id
  const nav_link = document.querySelector(id_);
  // hide element
  nav_link.classList.remove("displayed");
};

window.openMenu = function (id_) {
  // select an element by id
  const menu = document.querySelector(id_);
  // show element
  menu.classList.add("displayed");
};

window.closeMenu = function (id_) {
  // select an element by id
  const menu = document.querySelector(id_);
  // hide element
  menu.classList.remove("displayed");
};

/******************** End Navigator Section */
